@import url(https://fonts.googleapis.com/css?family=Heebo);
@import url(https://fonts.googleapis.com/css?family=Raleway);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, html {
  font-family: 'Heebo', sans-serif;
  color: #757575;
  background: #f0f0f0; }

.title {
  font-family: 'Raleway', sans-serif;
  font-size: 2em;
  margin-bottom: 20px; }

.btn {
  padding: 10px 20px;
  background: #46c3bd;
  color: white;
  border: none; }
  .btn:hover {
    cursor: pointer;
    box-shadow: 10px 10px 32px -23px rgba(0, 0, 0, 0.75); }

.container {
  width: 90%;
  margin: 0 auto; }

@media (min-width: 62rem) {
  .container {
    width: 70%;
    max-width: 1000px; } }

.search__hero {
  background: #46c3bd;
  width: 100%;
  padding: 20px 0; }

.search__hero__logo {
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  display: block; }

.search__hero__input {
  display: block;
  margin: 0 auto;
  border: none;
  border-radius: 5px;
  width: 80%;
  max-width: 500px;
  padding: 20px;
  text-align: center; }

.search__events {
  background: #f0f0f0;
  height: 100%;
  padding: 40px 0; }

.search__events__grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px; }

@media (min-width: 62rem) {
  .search__events__grid {
    grid-template-columns: 1fr 1fr 1fr; } }

.searchEvent {
  background: white;
  text-align: center;
  max-width: 100%;
  box-shadow: 10px 10px 32px -23px rgba(0, 0, 0, 0.75); }
  .searchEvent h2 {
    color: #121212;
    margin-bottom: 10px; }
  .searchEvent img {
    width: 100%;
    max-width: 100%; }

.searchEvent__text__date, .searchEvent__text__venue {
  text-align: left;
  display: block;
  margin: 10px 0; }
  .searchEvent__text__date p, .searchEvent__text__venue p {
    display: inline;
    margin-left: 10px; }

.searchEvent__text__desc {
  height: 100px; }

.searchEvent__text {
  padding: 20px; }
  .searchEvent__text button {
    margin-top: 10px; }
    .searchEvent__text button:hover {
      box-shadow: 10px 10px 32px -23px rgba(0, 0, 0, 0.75);
      cursor: pointer; }

.event__hero {
  padding: 40px 0; }
  .event__hero img {
    height: 200px; }

.event__hero__info__text {
  text-align: left;
  display: block;
  margin: 10px 0; }
  .event__hero__info__text p {
    display: inline;
    margin-left: 10px; }

.event__hero__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 20px;
  border-bottom: 2px solid lightgrey; }

.event__desc {
  padding-bottom: 20px;
  border-bottom: 2px solid lightgrey; }

.event__artists {
  padding: 20px 0; }

@media (min-width: 62rem) {
  .event__hero__wrapper {
    grid-template-columns: 1fr 1fr; }
  .event__hero__img {
    justify-self: end; } }

.eventArtist {
  display: flex;
  align-content: center;
  padding: 20px 0;
  border-bottom: 2px solid lightgrey; }
  .eventArtist img {
    height: 100px;
    margin-right: 10px; }

.eventArtist__text {
  align-self: center; }

.artist {
  padding-bottom: 40px; }

.artist__wrapper {
  padding: 40px 0;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr; }
  .artist__wrapper img {
    max-width: 100%; }

.artist__text {
  padding: 0 20px; }

.artist__btn {
  text-align: center; }

.navbar {
  background: #46c3bd;
  padding: 10px; }
  .navbar img {
    max-width: 200px; }

.spinner {
  text-align: center;
  padding-top: 35vh; }

